export default {
	data() {
		return {
			screenHeight: 0, //屏幕高度
			isLoading: false, //防止频繁触发
			bottomDistinct: 0, //距离底部多少像素时触发
			pageObj: {
				page: 0,
				pagecount: 0
			},
			noMore: false,
			noData:false,
			isScroll: false,
			myFollowList: []
		}
	},
	methods: {
		showErrorImage(i){
			  this.myFollowList[i].image = this.$errorImage;  
		},
		// 关注该作者
		setFollow(i) {
			var that = this;
			this.$commonJs.isLogin().then(res => {
				this.$apiJs.setFollow({
					author_id: i.id,
				}).then(res => {
					this.getAllFans()
				})
			})
		},
		getAllFans() {
			var that = this;
			this.$apiJs.myFollow({
				page: this.pageObj.page + 1
			}).then(res => {
				var r_data = res.backlist;
				this.myFollowList = r_data;
			})
		}

	},
	mounted() {
		this.getAllFans();
	}
}
